import React, { forwardRef, useImperativeHandle, useState } from "react"
import { Modal, Form, Select, Input, message, Tooltip } from "antd"
import axios from "axios"
import { useTranslation } from "react-i18next"

const { Option } = Select

const SetRateModal = forwardRef((props, ref) => {
    const [port, setPort] = useState(0)
    const [prefix, setPrefix] = useState("")
    const [url, setUrl] = useState("")
    const [type, setType] = useState("")
    const [visible, setVisible] = useState(false)
    const [confirmLoading, setConfirmLoading] = useState(false)
    const [volumn, setVolumn] = useState("")
    const [speed, setSpeed] = useState("")

    const { t } = useTranslation()
    const { wrapRateLimitersInfo } = props

    useImperativeHandle(ref, () => ({
        showModal,
    }))

    const showModal = (port, prefix, url, type, init, speed) => {
        setPort(port)
        setPrefix(prefix)
        setUrl(url)
        setType(type)
        setVolumn(init)
        setSpeed(speed)
        setVisible(true)
    }

    const handleCancel = () => {
        setVisible(false)
    }

    const handleOk = () => {
        setConfirmLoading(true)
        setRate()
    }

    const setRate = () => {
        axios
            .post("/http/set/rate", {
                port: parseInt(port),
                prefix,
                url,
                type,
                volumn: parseInt(volumn),
                speed: parseInt(speed),
            })
            .then(
                ({ data }) => {
                    if (data === "success") {
                        message.success(t("other.success"))
                        wrapRateLimitersInfo();
                    } else if (data === "Not Leader") {
                        message.error(t("message.not_leader"))
                    } else {
                        message.error(data)
                    }
                    setVisible(false)
                    setConfirmLoading(false)
                },
                (err) => {
                    console.log(err)
                }
            )
    }
    const chooseType = (val) => {
        setType(val)
    }

    return (
        <Modal
            title={prefix + url}
            visible={visible}
            onOk={handleOk}
            confirmLoading={confirmLoading}
            onCancel={handleCancel}
        >
            <Form
                name="basic"
                labelCol={{ span: 4 }}
                wrapperCol={{ span: 16 }}
                autoComplete="off"
            >
                <Tooltip placement="left" title={t("rate.info.type")}>
                    <Form.Item label={t("rate.modal.type")}>
                        <Select
                            value={type}
                            style={{ width: 140 }}
                            onChange={chooseType}
                        >
                            <Option value="qps">{t("rate.modal.qps")}</Option>
                            {/* <Option value="concurrent">{t('rate.modal.concurrent')}</Option> */}
                        </Select>
                    </Form.Item>
                </Tooltip>
                <Tooltip placement="left" title={t("rate.info.volumn")}>
                    <Form.Item label={t("rate.modal.volumn")}>
                        <Input
                            value={volumn}
                            onChange={(e) => setVolumn(e.target.value)}
                        />
                    </Form.Item>
                </Tooltip>
                <Tooltip placement="left" title={t("rate.info.speed")}>
                    <Form.Item label={t("rate.modal.speed")}>
                        <Input
                            value={speed}
                            onChange={(e) => setSpeed(e.target.value)}
                        />
                    </Form.Item>
                </Tooltip>
            </Form>
        </Modal>
    )
})

export default SetRateModal
