import { message } from "antd"
import axios from "axios"
import React, { forwardRef, useImperativeHandle } from "react"
import "./index.scss"
import { useTranslation } from "react-i18next"

import AddHostModal from "../Modals/AddHostModal"
import ChangeLbModeModal from "../Modals/ChangeLbModeModal"

const BubbleDialog = forwardRef((props, ref) => {
    const [visible, setVisible] = React.useState(false)
    const [isLeaf, setIsLeaf] = React.useState(false)
    const [port, setPort] = React.useState(0)
    const [prefix, setPrefix] = React.useState("")
    const [node, setNode] = React.useState({})
    const [x, setX] = React.useState(0)
    const [y, setY] = React.useState(0)

    const dialog = React.useRef()
    const addHostRef = React.useRef()
    const changeLbModeModal = React.useRef()

    const { getHttpProxyInfo, wrapProxyInfo } = props

    const { t } = useTranslation()

    useImperativeHandle(ref, () => ({
        showModal,
        close,
    }))

    const showModal = (x, y, node) => {
        const { isLeaf, key } = node
        const { port } = props
        console.log(node)
        setNode(node)
        setPrefix(key)
        setVisible(true)
        setIsLeaf(isLeaf)
        setPort(port)
        setX(x)
        setY(y)
    }

    React.useEffect(() => {
        return () => {}
    }, [])

    const removePreix = () => {
        axios
            .post("/http/remove/prefix", {
                port: parseInt(port),
                prefix: node.key,
            })
            .then(
                ({ data }) => {
                    if (data === "success") {
                        message.success(t("other.success"))
                        wrapProxyInfo()
                    } else if (data === "Not Leader") {
                        message.error(t("message.not_leader"))
                    }
                    setVisible(false)
                },
                (err) => {
                    console.log(err)
                    setVisible(false)
                }
            )
    }

    const deleteHost = () => {
        axios
            .post("/http/remove/host", {
                port: parseInt(port),
                prefix: node.prefix,
                host: node.host,
            })
            .then(
                ({ data }) => {
                    if (data === "success") {
                        message.success(t("other.success"))
                        wrapProxyInfo()
                    } else if (data === "Not Leader") {
                        message.error(t("message.not_leader"))
                    } else {
                        message.error(data)
                    }
                    setVisible(false)
                },
                (err) => {
                    console.log(err)
                    setVisible(false)
                }
            )
    }

    const changeStatic = () => {
        axios
            .post("/http/change/static", {
                port: parseInt(port),
                prefix: node.key,
            })
            .then(
                ({ data }) => {
                    if (data === "success") {
                        message.success(t("other.success"))
                        wrapProxyInfo()
                    } else if (data === "Not Leader") {
                        message.error(t("message.not_leader"))
                    } else {
                        message.error(data)
                    }
                    setVisible(false)
                },
                (err) => {
                    console.log(err)
                    setVisible(false)
                }
            )
    }

    const changeLoadBalance = () => {
        changeLbModeModal.current.showModal(node, port)
    }

    const close = () => {
        setVisible(false)
    }

    return (
        <div
            className="bubbleDialog"
            hidden={!visible}
            ref={dialog}
            style={{ left: x - 256, top: y - 134 }}
        >
            <div
                className="remove-prefix"
                hidden={isLeaf}
                onClick={removePreix}
            >
                {t("bubble.remove_prefix")}
            </div>
            <div
                className="add-host"
                hidden={isLeaf}
                onClick={() => {
                    addHostRef.current.showModal(prefix, port)
                }}
            >
                {t("bubble.add_host")}
            </div>
            <div
                className="change-lb"
                hidden={isLeaf}
                onClick={changeLoadBalance}
            >
                {t("bubble.load_balance")}
            </div>
            <div className="delete-host" hidden={!isLeaf} onClick={deleteHost}>
                {t("bubble.delete_host")}
            </div>
            <div className="delete-host" hidden={isLeaf} onClick={changeStatic}>
                {node.isStatic ? t('bubble.non-static-resource') : t('bubble.static-resource')}
            </div>
            <div className="delete-host" onClick={() => navigator.clipboard.writeText(node.title)}>
                {t("bubble.copy")}
            </div>
            <div className="delete-host" onClick={() => setVisible(false)}>
                {t("bubble.close")}
            </div>
            <AddHostModal
                ref={addHostRef}
                close={close}
                port={port}
                pattern={node.key}
            />
            <ChangeLbModeModal ref={changeLbModeModal} wrapProxyInfo={wrapProxyInfo}/>
        </div>
    )
})

export default BubbleDialog
