import React, { useState, forwardRef, useImperativeHandle } from "react"
import { Modal, Form, Select, message } from "antd"
import axios from "axios"
import { useTranslation } from "react-i18next"

const { Option } = Select

const ChangeLbModeModal = forwardRef((props, ref) => {
    const [visible, setVisible] = useState(false)
    const [confirmLoading, setConfirmLoading] = useState(false)
    const [node, setNode] = useState({})
    const [lbMode, setLbMode] = useState(["round-robin"])
    const [balance, setBalance] = useState("")
    const [port, setPort] = useState(0)
    const { t } = useTranslation()

    const { wrapProxyInfo } = props

    useImperativeHandle(ref, () => ({
        showModal,
    }))

    React.useEffect(() => {
        getBalancerMode()
        return () => {}
    }, [])

    const showModal = (node, port) => {
        setPort(port)
        setNode(node)
        setVisible(true)
    }

    const handleChange = (val) => {
        setBalance(val)
    }

    const getBalancerMode = () => {
        axios
            .get("/http/lb/list")
            .then(({ data }) => {
                let options = []
                for (let i in data) {
                    const type = data[i]
                    let dom = (
                        <Option key={type} value={type}>
                            {t("balance_mode." + type)}
                        </Option>
                    )
                    options.push(dom)
                }
                setLbMode(options)
            })
            .catch((err) => console.log(err))
    }

    const handleOk = () => {
        setConfirmLoading(true)
        axios.post("/http/change/lb", {
            port: parseInt(port),
            prefix: node.key,
            lb: balance,
        }).then(({ data }) => {
            if (data === "success") {
                message.success(t("other.success"))
                wrapProxyInfo()
            } else if (data === "Not Leader") {
                message.error(t("message.not_leader"))
            } else {
                message.error(data)
            }
            setVisible(false)
            setConfirmLoading(false)
        }).catch(err => {
            console.log(err)
            setVisible(false)
            setConfirmLoading(false)
        })
    }

    const handleCancel = () => {
        setVisible(false)
    }

    return (
        <Modal
            title={t("path_manage.modal.balance")}
            visible={visible}
            onOk={handleOk}
            confirmLoading={confirmLoading}
            onCancel={handleCancel}
        >
            <Form
                name="basic"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 14 }}
                autoComplete="off"
            >
                <Form.Item label={t("path_manage.modal.balance_mode")}>
                    <Select
                        defaultValue={node.lb}
                        style={{ width: 180 }}
                        onChange={handleChange}
                    >
                        {/* <Option value="round-robin">{t('index.modal.round-robin')}</Option> */}
                        {lbMode}
                    </Select>
                </Form.Item>
            </Form>
        </Modal>
    )
})

export default ChangeLbModeModal
