import React from "react";
import "./index.scss";
import { useTranslation } from "react-i18next";
import { Button, message, Space, Table, Tag } from "antd";
import { useState, useEffect } from "react";
import axios from "axios";
import AddPortModal from "../../components/Modals/AddPortModal";
import { useRef } from "react";

const HttpProxy = (props) => {
    const { t } = useTranslation();
    const { history } = props;
    const addPortRef = useRef();
    const columns = [
        {
            title: t("httpProxy.port"),
            dataIndex: "key",
            key: "key",
        },
        {
            title: t("httpProxy.workMode"),
            dataIndex: "mode",
            key: "mode",
        },
        {
            title: t("httpProxy.state"),
            dataIndex: "state",
            key: "state",
            render: (_, record) => (
                <>
                    {record.state ? (
                        <Tag color="green">{t("httpProxy.start")}</Tag>
                    ) : (
                        <Tag color="red">{t("httpProxy.stop")}</Tag>
                    )}
                </>
            ),
        },
        {
            title: t("httpProxy.action"),
            key: "action",
            render: (_, record) => (
                <Space size="middle">
                    <a onClick={() => {
                        history.push(`/http-proxy-detail/${record.key}`)
                    }}>{t("httpProxy.manage")}</a>
                    <a onClick={() => {
                        changeHttpProxyState(record.key, 1);
                    }}>{t("httpProxy.run")}</a>
                    <a onClick={() => {
                        changeHttpProxyState(record.key, 2);
                    }}>{t("httpProxy.stop")}</a>
                    <a onClick={() => {
                        changeHttpProxyState(record.key, 0);
                    }}>{t("httpProxy.delete")}</a>
                </Space>
            ),
        },
    ];
    const [data, setData] = useState([]);
    const [proxyMapping, setProxyMapping] = useState();

    useEffect(() => {
        getProxyInfo();
        // console.log(props);
        return () => {};
    }, []);

    const getProxyInfo = () => {
        let list = [];
        let mp = new Map();
        axios
            .get("/http_proxy_info")
            .then(({ data }) => {
                console.log(data);
                for (let port in data) {
                    const proxy = {
                        key: port,
                        mode: data[port]["PrefixerType"],
                        state: data[port]["Available"],
                    };
                    mp[port] = data[port];
                    list.push(proxy);
                }
                setData(list);
                setProxyMapping(mp);
            })
            .catch((err) => {
                console.log(err);
            });
    };

    const changeHttpProxyState = (port, state) => {
        axios.post("/http/change", {
            port: parseInt(port),
            state: state 
        }).then(({data}) => {
            if (data === "success") {
                message.success(t('other.success'));
                getProxyInfo();
            } else if (data === "Not Leader") {
                message.error(t("message.not_leader"))
            } else {
                message.error(data)
            }
        }).catch(err => {
            console.log(err);
        })
    }

    return (
        <div className="http-proxy">
            <div className="append-btn">
                <Button type="primary" onClick={() => addPortRef.current.showModal()}></Button>
            </div>
            <Table columns={columns} dataSource={data} />
            <AddPortModal ref={ addPortRef } getProxyInfo = { getProxyInfo }/>
        </div>
    );
};

export default HttpProxy;
