import React, { useState, forwardRef, useImperativeHandle } from 'react'
import { Modal, Form, Input, Select, message } from 'antd';
import axios from 'axios';
import { useTranslation } from 'react-i18next'

const { Option } = Select;
const AddPortModal = forwardRef((props, ref) => {
    const [visible, setVisible] = useState(false);
    const [confirmLoading, setConfirmLoading] = useState(false);
    const [pType, setPType] = useState([]);
    const [prefixType, setPrefixType] = useState("hash-prefixer");
    const [port, setPort] = useState();

    const { t } = useTranslation();
    const { getProxyInfo } = props;
    useImperativeHandle(ref, () => ({
        showModal
    }));

    React.useEffect(() => {
        initPType();
        return () => {

        }
    }, [])

    const initPType = () => {
        let options = ['hash-prefixer'];
        for (let i in options) {
            const type = options[i]
            let dom = (
                <Option key={type} value={type}>
                    {t('httpProxy.modal.' + type)}
                </Option>
            )
            options.push(dom)
        }
        setPType(options);
    }

    const showModal = () => {
        setVisible(true);
    }

    const handleOk = () => {
        setConfirmLoading(true);
        axios.post('/http/add/proxy', {
            port: parseInt(port),
            prefixType,
        }).then(
            ({ data }) => {
                if (data === "success") {
                    message.success(t("other.success"))
                    getProxyInfo();
                } else if (data === "Not Leader") {
                    message.error(t("message.not_leader"))
                } else {
                    message.error(data)
                }
                setVisible(false)
                setConfirmLoading(false)
            },
            err => {
                setVisible(false)
                setConfirmLoading(false)
                console.log(err)
            }
        )
    }

    const handleCancel = () => {
        setVisible(false);
    }
    return (
        <Modal
            title={t('httpProxy.modal.add-proxy')}
            visible={visible}
            onOk={handleOk}
            confirmLoading={confirmLoading}
            onCancel={handleCancel}
        >
            <Form
                name="basic"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 14 }}
                autoComplete="off"
            >
                <Form.Item
                    label={t('httpProxy.modal.port')}
                >
                    <Input value={port} onChange={(e) => setPort(e.target.value)} />
                </Form.Item>
                <Form.Item
                    label={t('httpProxy.modal.prefix-type')}
                >
                    <Select
                        defaultValue="hash-prefixer"
                        style={{ width: 180 }}
                        onChange={setPrefixType}
                    >
                        {/* <Option value="round-robin">{t('index.modal.round-robin')}</Option> */}
                        {pType}
                    </Select>
                </Form.Item>
            </Form>
        </Modal>
    )
});

export default AddPortModal;