import React from "react"
import "./index.scss"
import { useEffect, useState, useRef } from "react"
import { useTranslation } from "react-i18next"
import axios from "axios"
import { Button, Table, Space, Popconfirm, message } from "antd"
// 
import AccessControlInsertModal from "../../components/Modals/AccessControlInsertModal"
import AccessControlDeleteModal from "../../components/Modals/AccessContorlDeleteModal"

const { Column } = Table;

const AclManage = (props) => {
    const { getHttpProxyInfo, port } = props
    const { t } = useTranslation()

    const [acl, setAclList] = useState([])

    const insertModal = useRef(null)
    const deleteModal = useRef(null)

    useEffect(() => {
        wrapAclList();
        return () => {

        }
    }, []);

    const wrapAclList = () => {
        getHttpProxyInfo((data) => {
            let acl = []
            let idx = 1
            for (let ip in data.Acl.record) {
                acl.push({
                    key: idx,
                    ipAddress: ip,
                })
                idx++
            }
            setAclList(acl)
        })
    }

    const confirm = (val) => {
        const { ipAddress } = val
        axios
            .post("/http/remove/black-list", {
                port: parseInt(port),
                ips: [ipAddress],
            })
            .then(
                ({ data }) => {
                    if (data === "success") {
                        message.success(t("other.success"))
                        wrapAclList()
                    } else if (data === "Not Leader") {
                        message.error(t("message.not_leader"))
                    }
                },
                (err) => console.log(err)
            )
    }

    return (
        <div className="accessList">
            <div className="handle-bar">
                <Button
                    type="primary"
                    onClick={() => insertModal.current.showModal()}
                >
                    {t("acl.insert")}
                </Button>
            </div>
            <Table dataSource={acl}>
                <Column title="#" dataIndex="key" key="key" />
                <Column
                    title={t("acl.ipAddress")}
                    dataIndex="ipAddress"
                    key="key"
                />
                <Column
                    title={t("acl.action")}
                    key="action"
                    render={(_, record) => (
                        <Space size="middle">
                            <Popconfirm
                                title={t("acl.confirm")}
                                okText="Yes"
                                cancelText="No"
                                onConfirm={() => confirm(record)}
                            >
                                <a>{t("acl.delete")}</a>
                            </Popconfirm>
                        </Space>
                    )}
                />
            </Table>
            <AccessControlInsertModal
                ref={insertModal}
                getHttpProxyInfo={getHttpProxyInfo}
                port={parseInt(port)}
                wrapAclList={wrapAclList}
            />
            <AccessControlDeleteModal
                ref={deleteModal}
                getHttpProxyInfo={getHttpProxyInfo}
                wrapAclList={wrapAclList}
            />
        </div>
    )
}

export default AclManage
