import React from "react"
import "./index.scss"
import { useEffect, useState } from "react"
import { useTranslation } from "react-i18next"
import { Table, Tabs } from "antd"
import axios from "axios"
// 
import RateManage from "../../components/RateManage"
import PathManage from "../../components/PathManage"
import AclManage from "../../components/AclManage"

const HttpProxyDetail = (props) => {
    const { match } = props
    const { t } = useTranslation()
    const [port, setPort] = useState(0)

    useEffect(() => {
        setPort(match.params.port)
        return () => {}
    }, [])

    const getHttpProxyInfo = (callback) => {
        axios.get(`/http/proxy?port=${match.params.port}`).then(
            ({ data }) => {
                callback(data);
            },
            (err) => {
                console.log(err)
            }
        )
    }

    return (
        <div className="http-proxy-detail">
            <Tabs className="tabs">
                <Tabs.TabPane className="tabs-pane" tab={t("httpProxyDetail.paths_manage")} key="paths">
                    <PathManage getHttpProxyInfo={getHttpProxyInfo} port={port}/>
                </Tabs.TabPane>
                <Tabs.TabPane tab={t("httpProxyDetail.acl_manage")} key="acl">
                    <AclManage getHttpProxyInfo={getHttpProxyInfo} port={port}/>
                </Tabs.TabPane>
                <Tabs.TabPane tab={t("httpProxyDetail.rate_manage")} key="rate">
                    <RateManage getHttpProxyInfo={getHttpProxyInfo} port={port}/>
                </Tabs.TabPane>
            </Tabs>
        </div>
    )
}

export default HttpProxyDetail
