import React, {
    forwardRef,
    useImperativeHandle,
    useState,
    useEffect,
} from "react"
import { Modal, Form, Input, Button, Select, message, Checkbox } from "antd"
import { MinusCircleOutlined, PlusOutlined } from "@ant-design/icons"
import axios from "axios"
import { useTranslation } from "react-i18next"
const { Option } = Select

const AddProxyModal = forwardRef((props, ref) => {
    const [visible, setVisible] = useState(false)
    const [confirmLoading, setConfirmLoading] = useState(false)
    const [prefix, setPattern] = useState("")
    const [proxyPass, setPass] = useState([])
    const [balanceMode, setBalance] = useState("round-robin")
    const [lbMode, setLbMode] = useState(["round-robin"])
    const [isStatic, setIsStatic] = useState(false);
    const { t } = useTranslation()
    const { wrapProxyInfo } = props
    useImperativeHandle(ref, () => ({
        showModal,
    }))

    useEffect(() => {
        getBalancerMode()
        return () => {}
    }, [])

    const getBalancerMode = () => {
        axios
            .get("/http/lb/list")
            .then(({ data }) => {
                let options = []
                for (let i in data) {
                    const type = data[i]
                    let dom = (
                        <Option key={type} value={type}>
                            {t("balance_mode." + type)}
                        </Option>
                    )
                    options.push(dom)
                }
                setLbMode(options)
            })
            .catch((err) => console.log(err))
    }

    const showModal = () => {
        setVisible(true)
    }

    const handleChange = (val) => {
        setBalance(val)
    }

    const handleOk = () => {
        setConfirmLoading(true)
        axios
            .post("/http/add/prefix", {
                port: parseInt(props.port),
                prefix,
                proxyPass,
                balanceType: balanceMode,
                static: isStatic
            })
            .then(
                ({ data }) => {
                    if (data === "success") {
                        message.success(t("other.success"))
                        wrapProxyInfo()
                    } else if (data === "Not Leader") {
                        message.error(t("message.not_leader"))
                    } else {
                        message.error(data)
                    }
                    setVisible(false)
                    setConfirmLoading(false)
                },
                (err) => {
                    setVisible(false)
                    setConfirmLoading(false)
                    console.log(err)
                }
            )
    }

    const handleCancel = () => {
        setVisible(false)
    }

    return (
        <Modal
            title={t("path_manage.modal.add_proxy")}
            visible={visible}
            onOk={handleOk}
            confirmLoading={confirmLoading}
            onCancel={handleCancel}
        >
            <Form
                name="basic"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 14 }}
                autoComplete="off"
            >
                <Form.Item label={t("path_manage.modal.pattern")}>
                    <Input
                        value={prefix}
                        onChange={(e) => setPattern(e.target.value)}
                    />
                </Form.Item>
                <Form.Item label={t("path_manage.modal.balance")}>
                    <Select
                        defaultValue="round-robin"
                        style={{ width: 180 }}
                        onChange={handleChange}
                    >
                        {/* <Option value="round-robin">{t('index.modal.round-robin')}</Option> */}
                        {lbMode}
                    </Select>
                </Form.Item>
                <Form.Item label={t("path_manage.modal.is-static")}>
                    <Checkbox onChange={(e) => setIsStatic(e.target.checked)}></Checkbox>
                </Form.Item>
                <Form.List name="names">
                    {(fields, { add, remove }, { errors }) => (
                        <>
                            {fields.map((field, index) => (
                                <Form.Item
                                    label={t("path_manage.modal.address")}
                                    required={false}
                                    key={field.key}
                                >
                                    {/* <Form.Item>
                                <Input placeholder="Proxy address"/>
                            </Form.Item> */}
                                    <Input
                                        placeholder={t(
                                            "path_manage.modal.hint"
                                        )}
                                        style={{ width: 250 }}
                                        onChange={(e) => {
                                            let proxyAddress = proxyPass
                                            proxyAddress[index] = e.target.value
                                            setPass(proxyAddress)
                                        }}
                                    />
                                    <MinusCircleOutlined
                                        style={{ marginLeft: 10 }}
                                        className="dynamic-delete-button"
                                        onClick={() => {
                                            remove(field.name)
                                            let pass = proxyPass
                                            pass.splice(index, 1)
                                            setPass(pass)
                                        }}
                                    />
                                </Form.Item>
                            ))}
                            <Form.Item>
                                <Button
                                    type="dashed"
                                    onClick={() => {
                                        add()
                                        let pass = proxyPass
                                        pass.push("")
                                        setPass(pass)
                                    }}
                                    icon={<PlusOutlined />}
                                >
                                    {t("path_manage.modal.add")}
                                </Button>
                                <Form.ErrorList errors={errors} />
                            </Form.Item>
                        </>
                    )}
                </Form.List>
            </Form>
        </Modal>
    )
})

export default AddProxyModal
