import React, { useEffect, useState, forwardRef, useImperativeHandle } from 'react'
import { Modal, Radio, Form, Input, Button, Checkbox } from 'antd'
import axios from 'axios';
import { message } from 'antd';
import { useTranslation } from 'react-i18next'

const AccessControlDeleteModal = forwardRef((props, ref) => {
    const [visible, setVisible] = useState(false);
    const [confirmLoading, setConfirmLoading] = useState(false);
    const [ipAddress, setIpAddress] = useState("");
    const { t } = useTranslation();
    const { wrapAclList } = props
    useEffect(() => {
        return () => {

        }
    }, [])

    useImperativeHandle(ref, () => ({
        showModal: () => showModal()
    }))

    function showModal() {
        setVisible(true);
    }

    function handleOk() {
        setConfirmLoading(true);
        axios.post('/acl', {
            pattern: 0,
            ipAddress
        }).then(
            ({ data }) => {
                if (data.code === 200) {
                    message.success(t('acl.modal.success'));
                    wrapAclList();
                } else if (data === "Not Leader") {
                    message.error(t("message.not_leader"))
                } else {
                    message.error(data)
                }
                setVisible(false);
                setConfirmLoading(false);
            }, 
            err => {
                message.error(err);
                setVisible(false);
                setConfirmLoading(false);
            }
        );
    }

    function handleCancel() {
        setVisible(false);
    }

    return (
        <Modal
            title={t('acl.delete')}
            visible={visible}
            onOk={handleOk}
            confirmLoading={confirmLoading}
            onCancel={handleCancel}
        >
            <Form
                name="basic"
                labelCol={{ span: 4 }}
                wrapperCol={{ span: 28 }}
            >
                <Form.Item
                    label={t('acl.ipAddress')}
                >
                    <Input value={ipAddress} onChange={(e) => setIpAddress(e.target.value)}/>
                </Form.Item>
            </Form>
        </Modal>
    )
})

export default AccessControlDeleteModal;