import React, { useState, useEffect, forwardRef, useImperativeHandle } from 'react'
import { Modal, Radio, Form, Input } from 'antd'
import axios from 'axios';
import { message } from 'antd';
import { useTranslation } from 'react-i18next'

const AccessControlInsertModal = forwardRef((props, ref) => {
    const [visible, setVisible] = useState(false);
    const [confirmLoading, setConfirmLoading] = useState(false);
    const [kind, setKind] = useState(1);
    const [ipAddress, setIpAddress] = useState("");
    const [mask, setMask] = useState("");
    const { t } = useTranslation();
    const { wrapAclList } = props;
    useEffect(() => {
        return () => {

        }
    }, [])

    useImperativeHandle(ref, () => ({
        showModal: () => showModal()
    }))

    function showModal() {
        setVisible(true);
    }

    function handleOk() {
        const { getHttpProxyInfo, port } = props;
        setConfirmLoading(true);
        // 
        let ipNet;
        if (kind == 1) {
            ipNet = ipAddress + "/32";
        } else {
            ipNet = ipAddress + "/" + mask;
        }
        // console.log(mask, ipNet);
        axios.post('/http/add/black-list', {
            port, 
            ips: [ipNet]
        }).then(
            ({ data }) => {
                if (data === 'success') {
                    message.success(t('other.success'));
                    setIpAddress("")
                    wrapAclList();
                } else if (data === "Not Leader") {
                    message.error(t("message.not_leader"))
                } else {
                    message.error(data)
                }
                setVisible(false);
                setConfirmLoading(false);
            }, 
            err => {
                message.error(err);
                setVisible(false);
                setConfirmLoading(false);
            }
        );
    }

    function handleCancel() {
        setVisible(false);
    }

    return (
        <Modal
            title={t('acl.insert')}
            visible={visible}
            onOk={handleOk}
            confirmLoading={confirmLoading}
            onCancel={handleCancel}
        >
            <Form
                name="basic"
                labelCol={{ span: 4 }}
                wrapperCol={{ span: 28 }}
            >
                <Form.Item>
                    <Radio.Group value={kind} onChange={(e) => setKind(e.target.value)}>
                        <Radio value={1}>IPv4</Radio>
                        <Radio value={2}>{t('acl.modal.cidr')}</Radio>
                        <Radio value={3} disabled>IPv6</Radio>
                    </Radio.Group>
                </Form.Item>
                <Form.Item
                    label={t('acl.ipAddress')}
                >
                    <Input value={ipAddress} onChange={(e) => setIpAddress(e.target.value)}/>
                </Form.Item>

                <Form.Item
                    label={t('acl.modal.mask')}
                    hidden={kind == 1}
                >
                    <Input value={mask} onChange={(e) => setMask(e.target.value)}/>
                </Form.Item>
            </Form>
        </Modal>
    )
})

export default AccessControlInsertModal;