import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import "./App.scss";
import "./i18n/config";
import { withTranslation } from "react-i18next";

// 
import Sidebar from "./components/Sidebar";
import Index from "./pages/Index";
import Raft from "./pages/Raft";
import HttpProxy from "./pages/HttpProxy";
import Topbar from "./components/Topbar";
import AccessList from "./pages/AccessList";
import Footer from "./components/Footer";
import HttpProxyDetail from "./pages/HttpProxyDetail";

class App extends Component {
    componentDidMount() {}
    render() {
        return (
            <div className="app">
                <Topbar />
                <div className="middle">
                    <Sidebar />
                    <div className="router">
                        <Switch>
                            <Route path="/cluster" component={Raft} />
                            <Route path="/httpProxy" component={HttpProxy} />
                            <Route path="/acl" component={AccessList} />
                            <Route path="/http-proxy-detail/:port" component={HttpProxyDetail} />
                            <Route path="/" component={Index} />
                        </Switch>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}

export default withTranslation()(App);
