import React from "react"
import "./index.scss"
import { useTranslation } from "react-i18next"
import { Tree } from "antd"
import { useState, useRef } from "react"
import { useEffect } from "react"

// Components
import BubbleDialog from "../BubbleDialog"
import AddProxyModal from "../Modals/AddProxyModal"
import add from "../../assets/imgs/add.svg"

const PathManage = (props) => {
    const { getHttpProxyInfo, port } = props
    const { t } = useTranslation()

    const [routers, setRouters] = useState([])

    const bubbleDialog = useRef()
    const addProxyModal = useRef()

    useEffect(() => {
        wrapProxyInfo();
        return () => {}
    }, [])

    const wrapProxyInfo = () => {
        getHttpProxyInfo((data) => {
            let treeData = []
            for (let prefix in data.Paths) {
                const hosts = data.Paths[prefix]
                let prefixNode = {
                    title: prefix,
                    key: prefix,
                    isLeaf: false,
                    lb: data.BalancerType[prefix],
                    host: "",
                    isStatic: data.StaticMapping[prefix],
                    children: [],
                }
                for (let i = 0; i < hosts.length; i++) {
                    prefixNode.children.push({
                        title: hosts[i],
                        key: prefix + "/" + hosts[i],
                        prefix: prefix,
                        isLeaf: true,
                        children: [],
                        host: hosts[i],
                    })
                }
                treeData.push(prefixNode)
            }
            setRouters(treeData)
        })
    }

    const proxyClick = (e) => {
        bubbleDialog.current.showModal(e.event.clientX, e.event.clientY, e.node)
    }

    return (
        <div className="path-manage" onClick={() => bubbleDialog.current.close()}>
            <div className="title">
                {t("path_manage.proxy_address")}
                <img
                    className="add"
                    src={add}
                    alt=""
                    onClick={() => {
                        addProxyModal.current.showModal()
                    }}
                />
            </div>
            <Tree
                showIcon
                showLine={true}
                className="tree"
                onRightClick={(e) => proxyClick(e)}
                treeData={routers}
            />
            <BubbleDialog
                ref={bubbleDialog}
                port={port}
                getHttpProxyInfo={getHttpProxyInfo}
                wrapProxyInfo={wrapProxyInfo}
            />
            <AddProxyModal
                ref={addProxyModal}
                port={port}
                getHttpProxyInfo={getHttpProxyInfo}
                wrapProxyInfo={wrapProxyInfo}
            />
        </div>
    )
}

export default PathManage
