import { React, useRef, useState, useEffect } from "react"
import "./index.scss"
import { Collapse } from "antd"
import { useTranslation } from "react-i18next"

import SetRateModal from "../../components/Modals/SetRateModal"

const { Panel } = Collapse

const RateManage = (props) => {
    const [panels, setPanels] = useState(new Array())
    const setRateModal = useRef()

    const { t } = useTranslation()
    const { getHttpProxyInfo, port } = props

    useEffect(() => {
        wrapRateLimitersInfo()
        return () => {}
    }, [])

    const wrapRateLimitersInfo = () => {
        getHttpProxyInfo((data) => {
            const types = data.RateLimiterType
            createCollapseElement(types)
        })
    }

    const createCollapseElement = (types) => {
        let p = []

        for (let prefix in types) {
            const infos = types[prefix]
            let methodsElement = []
            for (let i in infos) {
                const { url, type, init, speed } = infos[i]
                let methodDom = (
                    <div
                        className="method"
                        key={url}
                        onClick={() => {
                            setRateModal.current.showModal(
                                port,
                                prefix,
                                url,
                                type,
                                init,
                                speed
                            )
                        }}
                    >
                        {url}
                    </div>
                )
                methodsElement.push(methodDom)
            }
            let panelDom = (
                <Panel header={prefix} key={prefix}>
                    {methodsElement}
                </Panel>
            )
            p.push(panelDom)
        }
        setPanels(p)
    }

    return (
        <div className="rate">
            <Collapse>{panels}</Collapse>
            <SetRateModal
                ref={setRateModal}
                wrapRateLimitersInfo={wrapRateLimitersInfo}
            />
        </div>
    )
}

export default RateManage
