import React, { useState, forwardRef, useImperativeHandle } from 'react'
import { Modal, Form, Input, Select, message } from 'antd';
import axios from 'axios';
import { useTranslation } from 'react-i18next'

const AddHostModal = forwardRef((props, ref) => {
    const [visible, setVisible] = useState(false);
    const [confirmLoading, setConfirmLoading] = useState(false);
    const [prefix, setPrefix] = useState("");
    const [host, setHost] = useState([]);
    const [port, setPort] = useState();

    const { t } = useTranslation();
    useImperativeHandle(ref, () => ({
        showModal
    }));

    React.useEffect(() => {
        return () => {

        }
    }, [])

    const showModal = (prefix, port) => {
        setVisible(true);
        setPrefix(prefix);
        setPort(port);
    }

    const handleOk = () => {
        setConfirmLoading(true);
        axios.post('/http/add/host', {
            port: parseInt(port),
            prefix,
            host
        }).then(
            ({ data }) => {
                if (data === "success") {
                    message.success(t('other.success'))
                } else if (data === "Not Leader") {
                    message.error(t("message.not_leader"))
                } else {
                    message.error(data)
                }
                setVisible(false);
            },
            err => {
                console.log(err);
                setVisible(false);
            }
        )
    }

    const handleCancel = () => {
        setVisible(false);
    }
    return (
        <Modal
            title={t('bubble.add_host')}
            visible={visible}
            onOk={handleOk}
            confirmLoading={confirmLoading}
            onCancel={handleCancel}
        >
            <Form
                name="basic"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 14 }}
                autoComplete="off"
            >
                <Form.Item
                    label={t('bubble.proxy_address')}
                >
                    <Input value={host} onChange={(e) => setHost(e.target.value)} />
                </Form.Item>
            </Form>
        </Modal>
    )
});

export default AddHostModal