import axios from "axios"
import React from "react"
import { useTranslation } from "react-i18next"
import { Spin, Descriptions } from "antd"
import "./index.scss"
// 
// 

export default function Index() {
    const [loading, setLoading] = React.useState(true)
    const [info, setInfo] = React.useState({
        server: {},
        raft: {},
    })
    const [sysInfo, setSysInfo] = React.useState({
        architecture: "",
        os: {},
        timezone: "",
        name: "",
    });
    const [memInfo, setMemInfo] = React.useState({
        available_bytes: 0,
        free_bytes: 0,
        total_bytes: 0,
        used_bytes: 0,
        virtual_free_bytes: 0,
        virtual_total_bytes: 0,
        virtual_used_bytes: 0,
    });

    const { t } = useTranslation()

    React.useEffect(() => {
        getNodeInfo()
        getMemInfo()
        return () => {}
    }, [])

    const getNodeInfo = () => {
        axios.get("/info").then(
            ({ data }) => {
                setInfo(data)
                setLoading(false)
            },
            (err) => {
                console.log(err)
            }
        )
        axios.get("/sysinfo").then(
            ({ data }) => {
                console.log(data);
                setSysInfo(data);
            },
            err => {
                console.log(err);
            }
        )
    }

    const getMemInfo = async () => {
        await axios
            .get("/meminfo")
            .then(({ data }) => {
                setMemInfo(data);
            })
            .catch((err) => {
                console.log(err)
            })
    }

    // function addProxy() {
    //     addProxyModal.current.showModal();
    // }

    // const proxyClick = (e) => {
    //     // console.log(e)
    //     // console.log(bubbleDialog.current)
    //     bubbleDialog.current.showModal(
    //         e.event.clientX,
    //         e.event.clientY,
    //         e.node
    //     );
    // };

    return (
        <div className="index">
            <Spin spinning={loading} size="large">
                <div className="desc">
                    <Descriptions
                        title={t("index.peer_info")}
                        bordered
                        column={2}
                    >
                        <Descriptions.Item label={t("index.cpu_arch")}>
                            {sysInfo.architecture}
                        </Descriptions.Item>
                        <Descriptions.Item label={t("index.os_type")}>
                            {sysInfo.os.type}
                        </Descriptions.Item>
                        <Descriptions.Item label={t("index.hostname")}>
                            {sysInfo.name}
                        </Descriptions.Item>
                        <Descriptions.Item label={t("index.timezone")}>
                            {sysInfo.timezone}
                        </Descriptions.Item>
                        <Descriptions.Item label={t("index.node_name")}>
                            {info.server.name}
                        </Descriptions.Item>
                        <Descriptions.Item label={t("index.http_port")}>
                            {info.server.httpport}
                        </Descriptions.Item>
                        <Descriptions.Item label={t("index.mode")}>
                            {info.server.mode === "stand" ? t("index.stand_mode") : t("index.cluster_mode")}
                        </Descriptions.Item>
                        <Descriptions.Item
                            label={t("index.proxy_num")}
                        >
                            {info.http_proxy}
                        </Descriptions.Item>
                        <Descriptions.Item
                            label={t("index.memory_rate")}
                        >
                            {parseInt(memInfo.available_bytes / memInfo.total_bytes * 100)}%
                        </Descriptions.Item>
                        <Descriptions.Item
                            label={t("index.virtual_memory_rate")}
                        >
                            {parseInt(memInfo.virtual_free_bytes / memInfo.virtual_total_bytes * 100)}%
                        </Descriptions.Item>
                        {info.server.mode === "cluster" ? (
                            <>
                                <Descriptions.Item
                                    label={t("index.role")}
                                >
                                    {info.role}
                                </Descriptions.Item>
                                <Descriptions.Item
                                    label={t("index.raft_leader_address")}
                                >
                                    {info.raft.leaderaddress}
                                </Descriptions.Item>
                                <Descriptions.Item
                                    label={t("index.raft_leader_id")}
                                >
                                    {info.leaderId}
                                </Descriptions.Item>
                                <Descriptions.Item
                                    label={t("index.raft_tcp_address")}
                                >
                                    {info.raft.tcpaddress}
                                </Descriptions.Item>
                            </>
                        ) : (
                            <></>
                        )}
                    </Descriptions>
                </div>
                {/* <div ref={graphicsRef} className="graphics">
                    <Gauge
                        ref={memRef}
                        width={600}
                        height={500}
                        {...memConfig}
                    />
                    <Gauge
                        ref={virMemRef}
                        width={600}
                        height={500}
                        {...virtualMemConfig}
                    />
                </div> */}
            </Spin>
        </div>
    )
}
